// üî• ÏÑ±Îä• ÏµúÏ†ÅÌôî: React.memo Ï†ÅÏö©
import React, { memo } from "react";
import { logger } from '../../utils/logger';

const GiftCouponModal = memo(function GiftCouponModal({
  showGiftCouponModal,
  setShowGiftCouponModal,
  recipients,
  giftRecipient,
  setGiftRecipient, // Ïù¥ Ìï®ÏàòÎäî DashboardÏóêÏÑú Ï†ÑÎã¨ÎêòÏñ¥Ïïº Ìï®
  giftAmount,
  setGiftAmount, // Ïù¥ Ìï®ÏàòÍ∞Ä DashboardÏóêÏÑú Ïò¨Î∞îÎ•¥Í≤å Ï†ÑÎã¨ÎêòÏñ¥Ïïº Ìï®
  handleGiftCoupon,
  currentCoupons,
  userId,
}) {
  if (!showGiftCouponModal) {
    return null;
  }

  const validRecipients = recipients ?? [];

  const closeModal = () => {
    setShowGiftCouponModal(false);
    // --- Ïò§Î•ò Î∞úÏÉù ÏßÄÏ†ê ---
    // setGiftAmountÏôÄ setGiftRecipientÍ∞Ä Ìï®Ïàò ÌòïÌÉúÎ°ú Ïò¨Î∞îÎ•¥Í≤å Ï†ÑÎã¨ÎêòÏóàÎäîÏßÄ ÌôïÏù∏ ÌïÑÏöî
    if (typeof setGiftAmount === "function") {
      setGiftAmount(""); // Î™®Îã¨ Îã´ÏùÑ Îïå ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî
    } else {
      logger.error("setGiftAmount prop is not a function!", setGiftAmount); // ÎîîÎ≤ÑÍπÖ Î°úÍ∑∏ Ï∂îÍ∞Ä
    }
    if (typeof setGiftRecipient === "function") {
      setGiftRecipient("");
    } else {
      logger.error(
        "setGiftRecipient prop is not a function!",
        setGiftRecipient
      ); // ÎîîÎ≤ÑÍπÖ Î°úÍ∑∏ Ï∂îÍ∞Ä
    }
  };

  return (
    <div
      className="modal-backdrop fixed inset-0 bg-black/50 flex justify-center items-center z-[1000]"
      onClick={closeModal} // Î∞∞Í≤Ω ÌÅ¥Î¶≠ Ïãú Îã´Í∏∞
    >
      <div
        className="gift-coupon-modal bg-white rounded-xl p-6 w-[90%] max-w-[400px] shadow-xl"
        onClick={(e) => e.stopPropagation()} // Î™®Îã¨ ÎÇ¥Î∂Ä ÌÅ¥Î¶≠ Ïãú Îã´Ìûò Î∞©ÏßÄ
      >
        <h3 className="mt-0 text-gray-700">Ïø†Ìè∞ ÏÑ†Î¨ºÌïòÍ∏∞</h3>
        <div className="mb-4 text-gray-500">
          <p>
            ÌòÑÏû¨ Î≥¥Ïú† Ïø†Ìè∞: <strong>{currentCoupons}Í∞ú</strong>
          </p>
        </div>
        <div className="form-group mb-4">
          <label className="block mb-2 font-medium text-gray-600">
            Î∞õÎäî ÏÇ¨Îûå
          </label>
          <select
            value={giftRecipient}
            // onChange Ìï∏Îì§Îü¨ÏóêÏÑú setGiftRecipientÍ∞Ä Ìï®ÏàòÏù∏ÏßÄ ÌôïÏù∏
            onChange={(e) =>
              typeof setGiftRecipient === "function"
                ? setGiftRecipient(e.target.value)
                : logger.error("setGiftRecipient is not a function")
            }
            className="w-full p-2.5 border border-gray-300 rounded-md text-sm"
          >
            <option value="">Î∞õÎäî Î∂ÑÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî</option>
            {validRecipients
              .filter((recipient) => recipient.id !== userId)
              .map((recipient) => (
                <option key={recipient.id} value={recipient.id}>
                  {recipient.name} ({recipient.id})
                </option>
              ))}
          </select>
        </div>
        <div className="form-group mb-6">
          <label className="block mb-2 font-medium text-gray-600">
            ÏÑ†Î¨ºÌï† Ïø†Ìè∞ ÏàòÎüâ
          </label>
          <input
            type="number"
            value={giftAmount}
            // onChange Ìï∏Îì§Îü¨ÏóêÏÑú setGiftAmountÍ∞Ä Ìï®ÏàòÏù∏ÏßÄ ÌôïÏù∏
            onChange={(e) =>
              typeof setGiftAmount === "function"
                ? setGiftAmount(e.target.value)
                : logger.error("setGiftAmount is not a function")
            }
            placeholder="ÏÑ†Î¨ºÌï† Ïø†Ìè∞ ÏàòÎüâÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            className="w-full p-2.5 border border-gray-300 rounded-md text-sm"
            max={currentCoupons}
            min="1"
          />
        </div>
        <div className="modal-actions flex justify-end gap-2.5">
          <button
            className="cancel-button px-4 py-2 bg-gray-100 text-gray-600 border-0 rounded-md cursor-pointer font-medium"
            onClick={closeModal}
          >
            Ï∑®ÏÜå
          </button>
          <button
            className="confirm-button px-4 py-2 bg-emerald-500 text-white border-0 rounded-md text-sm font-medium"
            onClick={handleGiftCoupon}
            disabled={
              !giftRecipient ||
              !giftAmount ||
              parseInt(giftAmount) <= 0 ||
              parseInt(giftAmount) > currentCoupons
            }
            style={{
              cursor:
                !giftRecipient ||
                !giftAmount ||
                parseInt(giftAmount) <= 0 ||
                parseInt(giftAmount) > currentCoupons
                  ? "not-allowed"
                  : "pointer",
              opacity:
                !giftRecipient ||
                !giftAmount ||
                parseInt(giftAmount) <= 0 ||
                parseInt(giftAmount) > currentCoupons
                  ? 0.7
                  : 1,
            }}
          >
            ÏÑ†Î¨ºÌïòÍ∏∞
          </button>
        </div>
      </div>
    </div>
  );
});

export default GiftCouponModal;
