// src/SellCouponModal.js
// üî• ÏÑ±Îä• ÏµúÏ†ÅÌôî: React.memo Ï†ÅÏö©
import React, { useState, memo } from "react";
import { logger } from '../../utils/logger';

const SellCouponModal = memo(function SellCouponModal({
  showSellCouponModal,
  setShowSellCouponModal,
  currentCoupons = 0,
  couponValue = 1000,
  sellAmount = "",
  setSellAmount,
  // Dashboard ÎòêÎäî MyAssetsÎ°úÎ∂ÄÌÑ∞ Ïã§Ï†ú ÌåêÎß§ Ï≤òÎ¶¨ Ìï®ÏàòÎ•º propsÎ°ú Ï†ÑÎã¨Î∞õÏäµÎãàÎã§.
  // Ïù¥Ï†Ñ ÎãµÎ≥ÄÏóêÏÑú Ïñ∏Í∏âÌñàÎìØÏù¥, Dashboard.jsÏóêÏÑúÎäî <SellCouponModal SellCoupon={handleSellCoupon} ... /> ÌòïÌÉúÎ°ú Ï†ÑÎã¨ÌïòÍ≥† ÏûàÏäµÎãàÎã§.
  // Ïù¥ prop Ïù¥Î¶ÑÏùÑ Í∑∏ÎåÄÎ°ú ÏÇ¨Ïö©Ìï©ÎãàÎã§.
  SellCoupon, // Ïã§Ï†ú ÌåêÎß§ Î°úÏßÅÏùÑ Ï≤òÎ¶¨ÌïòÎäî Ìï®Ïàò (Dashboard ÎòêÎäî MyAssets ÏóêÏÑú Ï†ÑÎã¨Î∞õÏùå)
}) {
  const [isProcessing, setIsProcessing] = useState(false);
  const [error, setError] = useState("");

  // Î™®Îã¨Ïù¥ ÌëúÏãúÎêòÏßÄ ÏïäÏùÑ ÎïåÎäî Î†åÎçîÎßÅÌïòÏßÄ ÏïäÏùå
  if (!showSellCouponModal) return null;

  // ÌåêÎß§ Í∏àÏï° Í≥ÑÏÇ∞
  const calculateValue = () => {
    const amount = parseInt(sellAmount);
    if (!isNaN(amount) && amount > 0) {
      return amount * couponValue;
    }
    return 0;
  };

  // handleSubmit Ìï®ÏàòÏóêÏÑú DashboardÏùò handleSellCoupon (propsÎ°ú Ï†ÑÎã¨Î∞õÏùÄ SellCoupon)ÏùÑ Ìò∏Ï∂úÌï©ÎãàÎã§.
  const handleSubmit = async (e) => {
    e.preventDefault();
    const amount = parseInt(sellAmount);

    // Ïò§Î•ò Î©îÏãúÏßÄ Ï¥àÍ∏∞Ìôî
    setError("");

    // Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨ Ï∂îÍ∞Ä
    if (!amount || isNaN(amount) || amount <= 0) {
      setError("Ïú†Ìö®Ìïú Ïø†Ìè∞ ÏàòÎüâÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    if (amount > currentCoupons) {
      setError("Î≥¥Ïú†Ìïú Ïø†Ìè∞Î≥¥Îã§ ÎßéÏùÄ ÏàòÎüâÏùÑ ÌåêÎß§Ìï† Ïàò ÏóÜÏäµÎãàÎã§.");
      return;
    }

    // SellCoupon propÏù¥ Ìï®ÏàòÏù∏ÏßÄ ÌôïÏù∏
    if (typeof SellCoupon !== "function") {
      setError("ÌåêÎß§ Ï≤òÎ¶¨ Ìï®ÏàòÍ∞Ä Ïò¨Î∞îÎ•¥Í≤å Ï†ÑÎã¨ÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.");
      logger.error("SellCoupon prop is not a function:", SellCoupon);
      // ÏÇ¨Ïö©ÏûêÏóêÍ≤å Ïù¥ Ïò§Î•òÎ•º ÏßÅÏ†ë ÏïåÎ¶¥ ÏàòÎèÑ ÏûàÏäµÎãàÎã§.
      // alert("ÌåêÎß§ Ï≤òÎ¶¨ Ï§ë ÏãúÏä§ÌÖú Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Í¥ÄÎ¶¨ÏûêÏóêÍ≤å Î¨∏ÏùòÌïòÏÑ∏Ïöî.");
      return;
    }

    setIsProcessing(true);

    try {
      // Dashboard ÎòêÎäî MyAssetsÎ°úÎ∂ÄÌÑ∞ Ï†ÑÎã¨Î∞õÏùÄ SellCoupon Ìï®ÏàòÎ•º Ìò∏Ï∂úÌï©ÎãàÎã§.
      // Dashboard.jsÏùò handleSellCoupon Ìï®ÏàòÎäî ÎÇ¥Î∂ÄÏ†ÅÏúºÎ°ú sellAmount ÏÉÅÌÉúÎ•º ÏÇ¨Ïö©ÌïòÎØÄÎ°ú,
      // Ìï¥Îãπ Ìï®ÏàòÎäî Î≥ÑÎèÑÏùò Ïù∏Ïûê ÏóÜÏù¥ Ìò∏Ï∂úÎê† Ïàò ÏûàÏäµÎãàÎã§ (Dashboard.js Íµ¨ÌòÑÏóê Îî∞Î¶Ñ).
      await SellCoupon(); // DashboardÏùò handleSellCoupon ÎòêÎäî MyAssetsÏùò handleSellCoupon Ìò∏Ï∂ú

      // ÌåêÎß§ ÏÑ±Í≥µ ÌõÑÏùò Ï≤òÎ¶¨Îäî Dashboard.js ÎòêÎäî MyAssets.jsÏùò handleSellCoupon Ìï®Ïàò ÎÇ¥Î∂ÄÏóêÏÑú
      // Î™®Îã¨ÏùÑ Îã´Í≥†, sellAmountÎ•º Ï¥àÍ∏∞ÌôîÌïòÎäî Îì±Ïùò ÏûëÏóÖÏùÑ ÏàòÌñâÌï† Í≤ÉÏúºÎ°ú Í∏∞ÎåÄÌï©ÎãàÎã§.
      // (Ïòà: setShowSellCouponModal(false); setSellAmount(""); Îì±ÏùÑ Ìï¥Îãπ Ìï®Ïàò ÎÇ¥ÏóêÏÑú Ìò∏Ï∂ú)
      // ÌòÑÏû¨ Dashboard.jsÏùò handleSellCoupon Ìï®ÏàòÎäî ÏÑ±Í≥µ Ïãú Î™®Îã¨ÏùÑ Îã´Í≥† sellAmountÎ•º Ï¥àÍ∏∞ÌôîÌïòÎèÑÎ°ù ÎêòÏñ¥ ÏûàÏäµÎãàÎã§.
    } catch (err) {
      logger.error("ÌåêÎß§ Ï≤òÎ¶¨ Ï§ë ÏòàÏÉÅÏπò Î™ªÌïú Ïò§Î•ò:", err);
      // err.messageÍ∞Ä ÏÇ¨Ïö©ÏûêÏóêÍ≤å Î≥¥Ïó¨Ï£ºÍ∏∞Ïóê Ï†ÅÏ†àÌïú ÎÇ¥Ïö©Ïù∏ÏßÄ ÌôïÏù∏ ÌïÑÏöî
      setError(
        err.message || "ÌåêÎß§ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî."
      );
    } finally {
      setIsProcessing(false);
      // ÏÑ±Í≥µ/Ïã§Ìå® Ïó¨Î∂ÄÏôÄ Í¥ÄÍ≥ÑÏóÜÏù¥ isProcessing ÏÉÅÌÉúÎäî falseÎ°ú Î≥ÄÍ≤Ω
      // ÏÑ±Í≥µ Ïãú Î™®Îã¨ Îã´Í∏∞ Î∞è ÏûÖÎ†• ÌïÑÎìú Ï¥àÍ∏∞ÌôîÎäî SellCoupon Ìï®ÏàòÍ∞Ä Îã¥Îãπ
    }
  };

  const handleCancel = () => {
    setSellAmount("");
    setError("");
    setShowSellCouponModal(false);
  };

  // ÌåêÎß§ Î≤ÑÌäº ÌôúÏÑ±Ìôî Ï°∞Í±¥
  const isInvalidAmount =
    !sellAmount ||
    isNaN(parseInt(sellAmount)) ||
    parseInt(sellAmount) <= 0 ||
    parseInt(sellAmount) > currentCoupons;

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-[1000]" onClick={handleCancel}>
      <div className="bg-white p-5 rounded-lg max-w-[500px] w-[90%] max-h-[90vh] overflow-y-auto shadow-md" onClick={(e) => e.stopPropagation()}>
        <h3 className="text-lg font-semibold mb-4 text-gray-800">
          Ïø†Ìè∞ ÌåêÎß§ÌïòÍ∏∞
        </h3>
        <p className="mb-2.5 text-sm text-gray-600">
          Ïø†Ìè∞ÏùÑ ÌåêÎß§ÌïòÍ≥† ÌòÑÍ∏àÏúºÎ°ú ÍµêÌôòÌïòÏÑ∏Ïöî.
        </p>
        <div className="flex justify-between mb-4 p-2.5 bg-gray-50 rounded-md text-[15px]">
          <span>ÌòÑÏû¨ Î≥¥Ïú† Ïø†Ìè∞:</span>
          <strong>{currentCoupons.toLocaleString()}</strong>
        </div>
        <div className="flex justify-between mb-4 p-2.5 bg-gray-50 rounded-md text-[15px] text-indigo-600">
          <span>1Ïø†Ìè∞ ÌåêÎß§Í∞Ä:</span>
          <strong>{couponValue.toLocaleString()}Ïõê</strong>
        </div>

        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label
              htmlFor="sellAmount"
              className="block mb-1 font-medium text-gray-700"
            >
              ÌåêÎß§Ìï† Ïø†Ìè∞ Ïàò:
            </label>
            <input
              id="sellAmount"
              type="number"
              value={sellAmount}
              onChange={(e) => setSellAmount(e.target.value)}
              min="1"
              max={currentCoupons} // ÏµúÎåÄÍ∞í ÏÑ§Ï†ï
              className="w-full px-3 py-2 border border-gray-300 rounded-md text-sm outline-none"
              disabled={isProcessing}
              required // HTML5 Í∏∞Î≥∏ Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
            />
          </div>

          {/* ÏóêÎü¨ Î©îÏãúÏßÄ ÌëúÏãú */}
          {error && (
            <div className="bg-red-50 border border-red-200 rounded-md px-4 py-3 mb-4 text-red-800 text-sm">
              <p className="m-0">‚ö†Ô∏è {error}</p>
            </div>
          )}

          <div className="mb-5 px-4 py-2.5 bg-indigo-50 rounded-md border-l-4 border-indigo-600">
            <div className="flex justify-between text-base font-semibold text-gray-800">
              <span>ÏòàÏÉÅ ÏàòÎ†πÏï°:</span>
              <span className="text-indigo-600">
                {calculateValue().toLocaleString()}Ïõê
              </span>
            </div>
          </div>

          <div className="flex justify-end gap-2.5 mt-5">
            <button
              type="button"
              onClick={handleCancel}
              className="px-4 py-2 bg-gray-100 text-gray-600 border-0 rounded-md text-sm cursor-pointer transition-colors duration-200"
              disabled={isProcessing}
            >
              Ï∑®ÏÜå
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-red-500 text-white border-0 rounded-md text-sm font-medium transition-all duration-200"
              style={{
                cursor:
                  isInvalidAmount || isProcessing ? "not-allowed" : "pointer",
                opacity: isInvalidAmount || isProcessing ? 0.6 : 1,
              }}
              disabled={isInvalidAmount || isProcessing}
            >
              {isProcessing ? "Ï≤òÎ¶¨ Ï§ë..." : "ÌåêÎß§ÌïòÍ∏∞"}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
});

export default SellCouponModal;
