// src/DonateCouponModal.js
// üî• ÏÑ±Îä• ÏµúÏ†ÅÌôî: React.memo Ï†ÅÏö©
import React, { useState, memo } from "react";
import { logger } from '../../utils/logger';

const DonateCouponModal = memo(function DonateCouponModal({
  showDonateModal,
  setShowDonateModal,
  currentCoupons,
  onDonate,
  classCode, // üî• userId, currentGoalIdÎäî Îçî Ïù¥ÏÉÅ ÌïÑÏöî ÏóÜÏùå
}) {
  const [donateAmount, setDonateAmount] = useState("");
  const [donateMessage, setDonateMessage] = useState("");
  const [isDonating, setIsDonating] = useState(false);

  const handleDonate = async () => {
    const amount = parseInt(donateAmount, 10);
    if (isNaN(amount) || amount <= 0) {
      alert("Ïò¨Î∞îÎ•∏ Ïø†Ìè∞ ÏàòÎüâÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }
    if (amount > currentCoupons) {
      alert("Î≥¥Ïú† Ïø†Ìè∞Î≥¥Îã§ ÎßéÏù¥ Í∏∞Î∂ÄÌï† Ïàò ÏóÜÏäµÎãàÎã§.");
      return;
    }

    setIsDonating(true);
    try {
      // onDonateÎäî MyAssets.jsÏùò handleDonateCoupon Ìï®Ïàò
      const success = await onDonate(amount, donateMessage);
      if (success) {
        // üî• ÏÑ±Í≥µ Ïãú Î™®Îã¨ÏùÑ Îã´Í≥† ÏÉÅÌÉúÎ•º Ï¥àÍ∏∞Ìôî
        setDonateAmount("");
        setDonateMessage("");
        setShowDonateModal(false);
      }
    } catch (error) {
      logger.error("[DonateCouponModal] Í∏∞Î∂Ä Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò:", error);
      alert("Í∏∞Î∂Ä Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      setIsDonating(false);
    }
  };

  const handleClose = () => {
    if (!isDonating) {
      setShowDonateModal(false);
      setDonateAmount("");
      setDonateMessage("");
    }
  };

  if (!showDonateModal) return null;

  return (
    <div
      className="fixed inset-0 bg-black/50 flex items-center justify-center z-[1000] p-5"
      onClick={handleClose}
    >
      <div
        className="bg-white rounded-xl shadow-lg w-full max-w-[500px] overflow-hidden"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Ìó§Îçî */}
        <div className="px-5 py-4 border-b border-gray-200 flex justify-between items-center bg-gray-50">
          <h3 className="m-0 text-lg font-semibold text-gray-800">
            Ïø†Ìè∞ Í∏∞Î∂ÄÌïòÍ∏∞
            {classCode && (
              <span className="ml-2 text-sm bg-blue-100 text-blue-800 px-2 py-1 rounded-xl font-medium">
                {classCode}
              </span>
            )}
          </h3>
          <button
            onClick={handleClose}
            disabled={isDonating}
            className={`bg-transparent border-0 text-xl text-gray-400 p-0 leading-none ${isDonating ? 'cursor-not-allowed' : 'cursor-pointer'}`}
            aria-label="Îã´Í∏∞"
          >
            &times;
          </button>
        </div>

        {/* ÎÇ¥Ïö© */}
        <div className="p-5">
          <div className="mb-5">
            <div className="flex justify-between items-center bg-indigo-50 px-4 py-3 rounded-lg border border-indigo-200">
              <span className="text-sm font-medium text-indigo-700">
                ÌòÑÏû¨ Î≥¥Ïú† Ïø†Ìè∞
              </span>
              <span className="text-base font-semibold text-indigo-600">
                {currentCoupons.toLocaleString()} Í∞ú
              </span>
            </div>
          </div>

          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1.5">
              Í∏∞Î∂ÄÌï† Ïø†Ìè∞ ÏàòÎüâ
            </label>
            <input
              type="number"
              min="1"
              max={currentCoupons}
              value={donateAmount}
              onChange={(e) => setDonateAmount(e.target.value)}
              disabled={isDonating}
              className={`w-full p-3 border border-gray-300 rounded-md text-base ${isDonating ? 'bg-gray-50 cursor-not-allowed' : 'bg-white cursor-text'}`}
              placeholder="Í∏∞Î∂ÄÌï† Ïø†Ìè∞ ÏàòÎüâÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            />
          </div>

          <div className="mb-5">
            <label className="block text-sm font-medium text-gray-700 mb-1.5">
              Í∏∞Î∂Ä Î©îÏãúÏßÄ (ÏÑ†ÌÉùÏÇ¨Ìï≠)
            </label>
            <textarea
              value={donateMessage}
              onChange={(e) => setDonateMessage(e.target.value)}
              disabled={isDonating}
              className={`w-full p-3 border border-gray-300 rounded-md text-sm resize-y min-h-[80px] ${isDonating ? 'bg-gray-50 cursor-not-allowed' : 'bg-white cursor-text'}`}
              placeholder="Í∏∞Î∂ÄÏôÄ Ìï®Íªò Ï†ÑÌï† Î©îÏãúÏßÄÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            />
          </div>

          {/* ÏòàÏÉÅ Í∏∞Î∂ÄÏï° ÌëúÏãú */}
          {donateAmount && !isNaN(parseInt(donateAmount, 10)) && (
            <div className="bg-green-50 px-4 py-3 rounded-lg border border-green-200 mb-5">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium text-green-800">
                  Í∏∞Î∂Ä ÏòàÏ†ï Ïø†Ìè∞
                </span>
                <span className="text-base font-semibold text-green-600">
                  {parseInt(donateAmount, 10).toLocaleString()} Í∞ú
                </span>
              </div>
            </div>
          )}
        </div>

        {/* Ìë∏ÌÑ∞ */}
        <div className="px-5 py-4 border-t border-gray-200 flex justify-end gap-3">
          <button
            onClick={handleClose}
            disabled={isDonating}
            className={`px-4 py-2 bg-gray-200 text-gray-700 border-0 rounded-md font-medium ${isDonating ? 'cursor-not-allowed opacity-60' : 'cursor-pointer opacity-100'}`}
          >
            Ï∑®ÏÜå
          </button>
          <button
            onClick={handleDonate}
            disabled={
              isDonating ||
              !donateAmount ||
              isNaN(parseInt(donateAmount, 10)) ||
              parseInt(donateAmount, 10) <= 0 ||
              parseInt(donateAmount, 10) > currentCoupons
            }
            style={{
              backgroundColor:
                isDonating ||
                !donateAmount ||
                isNaN(parseInt(donateAmount, 10)) ||
                parseInt(donateAmount, 10) <= 0 ||
                parseInt(donateAmount, 10) > currentCoupons
                  ? "#9ca3af"
                  : "#4f46e5",
            }}
            className={`px-4 py-2 text-white border-0 rounded-md font-medium ${
              isDonating ||
              !donateAmount ||
              isNaN(parseInt(donateAmount, 10)) ||
              parseInt(donateAmount, 10) <= 0 ||
              parseInt(donateAmount, 10) > currentCoupons
                ? "cursor-not-allowed"
                : "cursor-pointer"
            }`}
          >
            {isDonating ? "Í∏∞Î∂Ä Ï§ë..." : "Í∏∞Î∂ÄÌïòÍ∏∞"}
          </button>
        </div>
      </div>
    </div>
  );
});

export default DonateCouponModal;